import React from "react";
import {isDeploy} from "../../../ajax";
import "./styles.scss";

const SECTION_NUMBER = "12"

export default class Section extends React.Component {

    render() {
        const {width, t} = this.props;

        const isMobile = width <= 1100;
        const assist = isMobile ? {width: 122, height: 43} : {width: 160, height: 56};
        const visa = isMobile ? {width: 122, height: 69} : {width: 160, height: 90};
        const mastercard = isMobile ? {width: 122, height: 32} : {width: 160, height: 41};
        const belcart = isMobile ? {width: 122, height: 31} : {width: 160, height: 41};
        const american = isMobile ? {width: 122, height: 122} : {width: 160, height: 160};

        return (
            <div className={`section --s${SECTION_NUMBER} animated`}>
                <div className={`--container`}>
                    <div className={"hr-line"}/>
                    <div className={`--content`}>
                        <div className="-block">
                            <div className="--text">{t.text_1}</div>
                            <div className="--text">{t.text_2}</div>
                            <div className="--text">{t.text_3}</div>
                            <div className="--text">{t.text_4}</div>
                        </div>
                        <div className="-block">
                            <div className="--text -bold -no-padding">{t.text_5}</div>
                            <div className="--text">{t.text_6}</div>
                            <div className="--text -bold -no-padding">{t.text_7}</div>
                            <div className="--text" style={{marginBottom: isMobile ? 20 : 44}}>{t.text_8}</div>
                            <div className={"--text -no-padding"}><a href={`${isDeploy ? "/tigerwood" : ""}/ordering`} target={"_black"} className="-link --text -no-padding">{t.text_9}</a></div>
                            <div className={"--text -no-padding"}><a href={`${isDeploy ? "/tigerwood" : ""}/contract_terms`} target={"_black"} className="-link --text -no-padding">{t.text_10}</a></div>
                            <div className={"--text -no-padding"}><a href={`${isDeploy ? "/tigerwood" : ""}/delivery`} target={"_black"} className="-link --text -no-padding">{t.text_11}</a></div>
                            <div className={"--text -no-padding"}><a href={`${isDeploy ? "/tigerwood" : ""}/payments`} target={"_black"} className="-link --text -no-padding">{t.text_12}</a></div>
                        </div>
                        <div className="-block pictures">
                            <div className="left">
                                <img className={"assist"} {...assist} src="data:image/png;base64,"/>
                                <img className={"visa"} {...visa} src="data:image/png;base64,"/>
                                <img className={"mastercard"} {...mastercard} src="data:image/png;base64,"/>
                            </div>
                            <div className="right">
                                <img className={"belcart"} {...belcart} src="data:image/png;base64,"/>
                                <img className={"american"} {...american} src="data:image/png;base64,"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
